import { Component, OnInit } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { AuthService } from './../../auth/auth.service';
import { CategoryService } from './../../services/category.service';
import { LocationService } from './../../services/location.service';
import { NgoService } from './../../services/ngo.service';

import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { map, take } from 'rxjs/operators';

import * as $ from 'jquery';

@Component({
  selector: 'app-dashboard',
  templateUrl: './ngo.component.html',
  styleUrls: ['./ngo.component.css']
})  

export class NgoComponent implements OnInit {

  public ngos = new Observable<any[]>();
  public categories = new Observable<any[]>();
  public locations = new Observable<any[]>();
  public sub_locations: any;

  public ngoAddForm = new FormGroup({      
    'name': new FormControl(null, [Validators.required]),
    'description': new FormControl(null, [Validators.required]),
    'category': new FormControl(null, [Validators.required]),  
    'location': new FormControl(null, [Validators.required]),    
    'sub_location': new FormControl(null, [Validators.required]),   
    'image_url': new FormControl(null, [Validators.required]), 
    'payment_url': new FormControl(null, [Validators.required]), 
  });

  public ngoEditForm = new FormGroup({      
    'name': new FormControl(null, [Validators.required]),
    'description': new FormControl(null, [Validators.required]),
    'category': new FormControl(null, [Validators.required]),  
    'location': new FormControl(null, [Validators.required]),     
    'sub_location': new FormControl(null, [Validators.required]),   
    'image_url': new FormControl(null, [Validators.required]), 
    'payment_url': new FormControl(null, [Validators.required]),
  });
  
  public modalReference: NgbModalRef | undefined;
  ngo: any;

  constructor(
    private modalService: NgbModal,
    public authService: AuthService,
    public categoryService: CategoryService,
    public locationService: LocationService,
    public ngoService: NgoService,
    public afs: AngularFirestore
  ) {}

  ngOnInit(): void {
    this.ngos =  this.ngoService.list();   
    this.categories = this.categoryService.list();   
    this.locations = this.locationService.list();   
    
  }

  addNgo() {
    this.ngoService.add(this.ngoAddForm.value);
    if(this.modalReference) {
      this.modalReference.close();
    }
  }

  editNgo() {
    this.ngoService.edit(this.ngo.id, this.ngoEditForm.value);
    if(this.modalReference) {
      this.modalReference.close();
    }
  }

  deleteNgo(docid: string) {
    if(confirm("Are you sure to delete?")) {      
      this.ngoService.delete(docid);
    };
  }

  openModal(modal: any) {
    this.modalReference = this.modalService.open(modal);
    this.modalReference.result.then((result) => {
      // console.log("Closed!");
    }, (reason) => {
      // console.log("Dismissed!");
    });
  }

  openNgoModal(modal: any, doc: any) {     
    this.modalReference = this.modalService.open(modal);
    this.ngo = doc;
    this.getSubLocations(this.ngo.location, '');
    this.modalReference.result.then((result) => {
      // console.log("Closed!");
    }, (reason) => {
      // console.log("Dismissed!");
    });
  }

  getSubLocations(location: string, event:any) {
    let _location: string;
    if(location.length > 0) {
      _location = location;
    } else {
      _location = event.target.value;
    }
    if(_location != 'null') {
      this.locations.subscribe(locations => {
        locations.map(item => {
          if (item.name == _location) {
            if(item.sub_locations) {
              this.sub_locations = item.sub_locations;
            } else {
              this.sub_locations = [];
            }
          }
        })
      })
    } else {
      this.sub_locations = [];
    }
  }

  clipboard(url: string) {
    let temp = $("<input>");
    $("body").append(temp);
    temp.val(location.origin+'/#'+url).select();
    document.execCommand("copy");
    temp.remove();
    alert('URL Copied!');
  }
}
