import { Component, OnInit } from '@angular/core';

import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { AuthService } from './../../auth/auth.service';
import { CategoryService } from './../../services/category.service';

import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})  

export class DashboardComponent implements OnInit {

  public categories: any;

  public cateogryAddForm = new FormGroup({      
    'name': new FormControl(null, [Validators.required]),
    //'description': new FormControl(null, [Validators.required]),      
  });

  public cateogryEditForm = new FormGroup({      
    'name': new FormControl(null, [Validators.required]),
    //'description': new FormControl(null, [Validators.required]),      
  });
  
  public modalReference: NgbModalRef | undefined;
  category: any;

  constructor(
    private modalService: NgbModal,
    public authService: AuthService,
    public categoryService: CategoryService,
    public afs: AngularFirestore
  ) {}

  ngOnInit(): void {
    this.categories = this.categoryService.list();   
  }

  addCategory() {
    this.categoryService.add(this.cateogryAddForm.value);
    if(this.modalReference) {
      this.modalReference.close();
    }
  }

  editCategory() {
    this.categoryService.edit(this.category.id, this.cateogryEditForm.value);
    if(this.modalReference) {
      this.modalReference.close();
    }
  }

  deleteCategory(docid: string) {
    if(confirm("Are you sure to delete?")) {      
      this.categoryService.delete(docid);
    };
  }

  openModal(modal: any) {
    this.modalReference = this.modalService.open(modal);
  }

  openCategoryModal(modal: any, doc: any) {        
    this.modalReference = this.modalService.open(modal);
    this.category = doc;
    console.log(this.category);
  }
}
