import { Component, OnInit, Pipe } from '@angular/core';

import { Observable } from 'rxjs';

import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';

import { CategoryService } from './../../services/category.service';
import { LocationService } from './../../services/location.service';
import { NgoService } from './../../services/ngo.service';

import { WindowRefService } from '../../window-ref.service';

import * as $ from 'jquery';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-causes-list',
  templateUrl: './causes-list.component.html',
  styleUrls: ['./causes-list.component.css'],
  providers: [WindowRefService]
})

export class CausesListComponent implements OnInit {

  public ngos = new Observable<any[]>();
  public ngo:any; 
  public categories = new Observable<any[]>();
  public locations = new Observable<any[]>();
  public sub_locations: any;

  public filter: any;
  public modalReference: NgbModalRef | undefined;

  public saveFormGroup = new FormGroup({   
    'ngo': new FormControl(null, []),   
    'name': new FormControl(null, [Validators.required]),
    'anonymous_payment': new FormControl(null, [Validators.required]),
    'address': new FormControl(null, [Validators.required]),  
    'pan': new FormControl(null, [Validators.required]),     
    'monthly_donor': new FormControl(null, [Validators.required]),   
  });

  constructor(
    private modalService: NgbModal,
    public categoryService: CategoryService,
    public locationService: LocationService,
    public ngoService: NgoService,
    public afs: AngularFirestore,
    private winRef: WindowRefService
  ) { }    

  ngOnInit(): void {
    this.ngos =  this.ngoService.list();   

    this.categories = this.categoryService.list();   
    this.locations = this.locationService.list();   

    this.filter = { searchText: '', category: null, location: null, sub_location: null};

    // this.createRzpayOrder('jahok');
  }

  createRzpayOrder(data: any) {
    console.log(data);

    let order_id = 'ppi_HhAs9jYafC6KK2';
    // call api to create order_id

    this.payWithRazor(order_id);
  }

  payWithRazor(val: any) {
    const options: any = {
      key: 'rzp_live_xtzOdEgjSc8GXg',
      amount: 125500, // amount should be in paise format to display Rs 1255 without decimal point
      currency: 'INR',
      name: '', // company name or product name
      description: '',  // product description
      image: './assets/logo.png', // company logo or product image
      order_id: val, // order_id created by you in backend
      modal: {
        // We should prevent closing of the form when esc key is pressed.
        escape: false,
      },
      notes: {
        // include notes if any
      },
      theme: {
        color: '#0c238a'
      }
    };
    options.handler = ((response: any, error: any) => {
      options.response = response;
      console.log(response);
      console.log(options);
      // call your backend api to verify payment signature & capture transaction
    });
    options.modal.ondismiss = (() => {
      // handle the case when user closes the form while transaction is in progress
      console.log('Transaction cancelled.');
    });
    const rzp = new this.winRef.nativeWindow.Razorpay(options);
    rzp.open();
  }

  getSubLocations(location: string, event:any) {
    let _location: string;
    if(location.length > 0) {
      _location = location;
    } else {
      _location = event.target.value;
    }
    if(_location != 'null') {
      this.locations.subscribe(locations => {
        locations.map(item => {
          if (item.name == _location) {
            if(item.sub_locations) {
              this.sub_locations = item.sub_locations;
            } else {
              this.sub_locations = [];
            }
          }
        })
      })
    } else {
      this.sub_locations = [];
    }
  }

  filterNgos() {
    console.log(this.filter.searchText);
    this.ngos.subscribe((ngos) => {
      return ngos.filter((element, index) => {
        element.name == this.filter.searchText;
      });
    })
  }

  openModal(modal: any, ngo: any) {
    this.ngo = ngo;
    this.modalReference = this.modalService.open(modal, { size: 'lg' });
  }

  saveForm() {
    // console.log(this.saveFormGroup.value);
    this.ngoService.saveData(this.saveFormGroup.value);
    if(this.modalReference) {
      this.saveFormGroup.reset();
      this.modalReference.close();
    }
  }
}
